<template>
  <div class="tooltip">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;button id="test"  style="margin:80px"&gt;张学友&lt;/button&gt;
&lt;script type="text/javascript"&gt;
    import tooltip from "cy_static/tooltip";
    tooltip("#test", {
      content : "香港特别行政区华语歌手",
      placement : "top left"
    });
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">tooltip 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import tooltip from "cy_static/tooltip"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;button id="test"&gt;张学友&lt;/button&gt;
&lt;script type="text/javascript"&gt;
    import tooltip from "cy_static/tooltip";
    tooltip("#test", {
      content : "香港特别行政区华语歌手",
      eventType : "click"
    });
&lt;/script&gt;
</xmp >

      </div>
       
      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
            <h2 class="cy_h2">构造方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>tooltip</a>(dom : selector|String, params : Object) : <a>Object</a></p>
						    <p>给元素绑定提示层；</p>
                <table class="table-props border">
                  <tr>
                    <th style="width:20%">参数名</th>
                    <th style="width:20%">类型</th>
                    <th style="width:20%">默认值</th>
                    <th style="width:30%">描述</th>
                    <th style="width:10%" class="text-c">必需</th>
                  </tr>
                  <tr>
										<td>dom</td>
										<td class="atn">Object|String</td>
										<td>--</td>
										<td>要绑定提示层的元素</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>params</td>
										<td class="atn">Object</td>
										<td>--</td>
										<td>提示层相关参数</td>
										<td class="text-c">Y</td>
									</tr>
                </table>
                <table class="table-props border">
                  <tr>
                    <th colspan="5">params 参数</th>
                  </tr>
                  <tr>
                    <th style="width:20%">参数名</th>
                    <th style="width:20%">类型</th>
                    <th style="width:20%">默认值</th>
                    <th style="width:30%">描述</th>
                    <th style="width:10%" class="text-c">必需</th>
                  </tr>
									 <tr>
										<td>content</td>
										<td class="atn">dom|String</td>
										<td>""</td>
										<td>提示层内容</td>
										<td class="text-c">Y</td>
									</tr>
									<tr>
										<td>delay</td>
										<td class="atn">Number</td>
										<td>"0"</td>
										<td>延迟时间</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>width</td>
										<td class="atn">Number</td>
										<td>100</td>
										<td>提示层宽</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>offset</td>
										<td class="atn">Array</td>
										<td>[0, 0]</td>
										<td>位置的偏移量</td>
										<td class="text-c">B</td>
									</tr>
									<tr>
										<td>disabled</td>
										<td class="atn">Boolean</td>
										<td>false</td>
										<td>是否可用</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>eventType</td>
										<td class="atn">String</td>
										<td>"hover"</td>
										<td>事件类型hover|click</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>placement</td>
										<td class="atn">String</td>
										<td>"bottom"</td>
										<td>出现位置 :
										<table>
                      <tr>
                        <td>
                          <div>top</div>
                          <div>right</div>
                          <div>bottom</div>
                          <div>left</div>
                        </td>
                        <td>
                          <div>top left</div>
                          <div>top center</div>
                          <div>top right</div>
                        </td>
                        <td>
                          <div>right top</div>
                          <div>right center</div>
                          <div>right bottom</div>
                        </td>
                        <td>
                          <div>bottom left</div>
                          <div>bottom center</div>
                          <div>bottom right</div>
                        </td>
                        <td>
                          <div>left top</div>
                          <div>left center</div>
                          <div>left bottom</div>
                        </td>
                      </tr>
                    </table>
										</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>className</td>
										<td class="atn">String</td>
										<td>""</td>
										<td>样式名</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>timeout</td>
										<td class="atn">Number</td>
										<td>500</td>
										<td>离开隐藏间隔时间</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>callback</td>
										<td class="atn">Function</td>
										<td>function(win) {}</td>
										<td></td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>onShow</td>
										<td class="atn">Function</td>
										<td>function(win) {}</td>
										<td>显示时触发的函数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>onHide</td>
										<td class="atn">Function</td>
										<td>function(win) {}</td>
										<td>隐藏时触发的函数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>onClose</td>
										<td class="atn">Function</td>
										<td>function(win) {}</td>
										<td>销毁时触发的函数</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>onClick</td>
										<td class="atn">Function</td>
										<td>function(win) {}</td>
										<td>点击时触发的函数</td>
										<td class="text-c">N</td>
									</tr>
                </table>
              </li>              
            </ul>
      </div>
      <div class="apiModule">
            <h2 class="cy_h2">实例方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>show</a>() : <a>String</a></p>
                <p>显示图层；</p>						
              </li>
              <li>
                <p><a>hide</a>() : <a>String</a></p>
                <p>隐藏图层；</p>						
              </li>
              <li>
                <p><a>close</a>() : <a>String</a></p>
                <p>销毁图层；</p>						
              </li>
              <li>
                <p><a>setDisabled</a>(disabled : Boolean) : <a>String</a></p>
                <p>开户与禁用提示层；</p>						
              </li>            
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'tooltip',
  data () {
    return {
      msg: 'tooltip',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .table-props table{white-space: nowrap;}
  .cy_example .codeArea{height:240px}
  .cy_example .iframeBox{flex:10}
</style>



